*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    transition: all .3s ease;
}

a{
    color: #555;
}

body{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.active {
    color: #333;
    background-color: antiquewhite;
    font-weight: bold;
}

nav{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 4rem;
    background-color: #eee;
    padding-left: 1rem;
}

nav > a{
    padding: .7rem 1rem;
    border-radius: 1rem;
    font-weight: bold;
    border: 1px solid #44444410;
}

nav > a:hover {
    color: #333;
}

.home{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    background-color: #fff;
}

.article > img{
    margin-top: 2rem;
}

.articleParagraph{
    margin: 2rem 0 5rem;
    width: 70%;
    line-height: 2;
}

.booklayout{
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}